    </div>
  </div>
</template>
<script>
  export default{
    data(){
        return{
          endDate:""
        }
    },
    mounted(){

        this.getCurrentDate();
    },
    methods:{
      /**
       * 获取当前的年份
        */
      getCurrentDate(){
          let date = new Date();
          this.endDate = date.getFullYear();
      }
    }
  }
</script>

<style scoped>
  @import "../../assets/css/footer.css";
</style>



